body {
  margin: 0;
}

.container {
  box-sizing: border-box;
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  justify-content: center;
  width: 200px;
  height: 408px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.container__photo {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 134px;
  margin: 0 3px 40px 2px;
}

.container__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.container__code {
  color: #616070;
  text-align: left;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.container__feedback {
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 24px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.container__price {
  flex-direction: row;
  justify-content: space-between;
  margin-bottom: 8px;
  line-height: 18px;
  display: flex;
}

.container__price--price {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.container__price--sum {
  font-size: 16px;
  font-weight: 700;
}

.container__buttom {
  color: #fff;
  box-sizing: border-box;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  place-content: center;
  align-items: center;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.container__buttom:hover {
  color: #00acdc;
  background-color: #fff;
}

.stars {
  column-gap: 4px;
  display: flex;
}

.stars__star {
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars--4 .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars--4 .stars__star:nth-child(n+5) {
  background-image: url("star.822d561e.svg");
}
